body{
    background-color: rgba(209, 209, 209, 0.075);
    font-family: 'Inter', sans-serif;
    font-weight: 300;
}
#arrow-pic{ width: 48px;}
#top-btn{
    position: fixed;
    bottom: 65px;
    right: 5px;
    border: none;
    background-color: transparent;
    z-index: 0;
}
#top-btn:hover{
    transform: scale(1.2);
}
.header{
    position: fixed;
    height: 70px;
    background-image: url("https://images.alphacoders.com/201/201831.png");
    width: 100%;
    border-radius: 7px;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    z-index: 1;
    padding-bottom: 35px;
    padding-top: 5px;
}
h1{
    font-family: 'Pokemon Solid', sans-serif;   
    background-image: url("https://images.alphacoders.com/201/201831.png");                                 
}
#logo{    width: 160px;}
.pg-btn:hover{ transform: scale(1.2);}
.pg-btn{ 
    margin-top: 60px;
    align-items: center;
    -webkit-appearance: none;
            appearance: none;
    background-color: #FCFCFD;
    border-radius: 4px;
    border-width: 0;
    box-shadow: rgba(45, 35, 66, 0.4) 0 2px 4px,rgba(45, 35, 66, 0.3) 0 7px 13px -3px,#D6D6E7 0 -3px 0 inset;
    box-sizing: border-box;
    color: #36395A;
    cursor: pointer;
    display: inline-flex;
    height: 22px;
    justify-content: center;
    line-height: 1;
    list-style: none;
    overflow: hidden;
    padding-left: 6px;
    padding-right: 6px;
    position: relative;
    text-align: left;
    text-decoration: none;
    transition: box-shadow .15s,transform .15s;
    -ms-user-select: none;
        user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    white-space: nowrap;
    will-change: box-shadow,transform;
    font-size: 14px;
}
#page-num{
    position: fixed;
    top: 65px;
    font-family: 'Pokemon Solid', sans-serif;   
}
.bottom-pic{
    animation: scale-in-center 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
.bottomPoke{
    display: flex;
    margin-left: 5px;
    margin-right: 5px;
    border-radius: 30px;
    animation: scale-in-center 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
#bottom{
    display: flex;
    flex-direction: row;
    flex-shrink: calc(.8);
    position: fixed ;
    bottom : -50px;
    left: 50%;
    transform: translateX(-50%);
    padding-right: 10px;
    padding-left: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    border-radius: 10px;
}
#bottom:hover{ bottom: 0;  }
@media (max-width: 550px ){
    #bottom{
        display: flex;
        flex-direction: column;
        position: fixed ;
        top:50%;
        left: 0px;
        transform: translateY(-50%);
        max-height: 300px;
    }
    #bottom:hover{
        transform:scale(2);
        bottom: auto;
        margin-top: -70px;
        padding-top: 10px;
        margin-left: 20px;
    }
    .bottomPoke{
        margin-top: 3px;
        margin-bottom: 3px;
    }
    .bottom-pic{ width: 30px; }
}
  @keyframes scale-in-center {
    0% {
      transform: scale(0);
      opacity: 1;
    }
    100% {
      transform: scale(1);
      opacity: 1;
    }
  }
  @keyframes slide-in-blurred-tl {
    0% {
      transform: translate(-1000px, -1000px) skew(80deg, 10deg);
      transform-origin: 100% 0%;
      filter: blur(40px);
      opacity: 0;
    }
    100% {
      transform: translate(0, 0) skew(0deg, 0deg);
      transform-origin: 50% 50%;
      filter: blur(0);
      opacity: 1;
    }
  }
  

body{
    font-family: 'Inter', sans-serif;
}
button{
    font-family: 'Inter', sans-serif;
}
#list-wrapper{
    display: flex;
    justify-content: center;
    background-image: url("https://images.alphacoders.com/201/201831.png");
    background-color: white;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    padding-left: 30px;
    padding-right: 30px;
}
#poke-list{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content:space-evenly;
    max-width: 1200px;
    margin-top: 110px;
    padding-bottom: 130px;
}
.poke-box {
    font-size: auto;
    width: 200px;
    border-radius: 7px; 
    color: white;
    margin: 3px;
    box-shadow: 5px 0px 5px 5px rgba(0, 0, 0, 0.295);
    animation: scale-in-center 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
.poke-name{
    font-size: large;
    text-align: center;
    font-weight:700;
    color: whitesmoke;
}
.poke-id{
    margin-left: 10px;
    padding-top: 13px;
    color: whitesmoke;
}
.poke-pic{
    display: flex;
    justify-content: center;
    background-color: whitesmoke;
    margin-left: 5px;
    margin-right: 5px;
}
.pic:hover{
    animation: rotation .3s  linear;
}
@keyframes rotation{
    from{ transform: rotate(0deg)}
    to {   transform: rotate(359deg) }
}
.poke-type{
    margin-left: 10px;
    font-size: 16px;
    color: whitesmoke;
}
.btn-box{
    margin-left: 140px;
    margin-bottom: 5px; 
}
.select-btn{
  align-items: center;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: transparent 0 0 0 3px,rgba(18, 18, 18, .1) 0 6px 20px;
  box-sizing: border-box;
  color: #121212;
  cursor: pointer;
  display: inline-flex;
  flex: 1 1 auto;
  font-size: 14px;
  font-weight: 500;
  justify-content: center;
  line-height: 1;
  margin: 0;
  outline: none;
  padding: 2px;
  text-align: center;
  text-decoration: none;
  transition: box-shadow .2s;
  white-space: nowrap;
  border: 0;
  -ms-user-select: none;
      user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}
.select-btn:hover {
  box-shadow: #121212 0 0 0 3px, transparent 0 0 0 0;
}
.dropdown{
    position: relative;
    padding-bottom: 1px;
}
.drop-button{
    width: 100%;
    background-color: transparent;
    border: none;
    padding-bottom: 15px;
    color: whitesmoke;
    font-size: 14px; 
}
.drop-button::after{
    content: '\25be';
    float: right;
}
.drop-button:hover{
    background-color: rgba(216, 191, 216, 0.26);
    font-size: large;
}
.drop-info{
  max-height: 0;
  overflow: hidden;
  transition: all cubic-bezier(0.55, 0.055, 0.675, 0.19);
}
.show {
    height: auto;
    max-height: 100%;
    transition : all 4.ms cubic-bezier(0.55, 0.055, 0.675, 0.19);
    color: black;
    background-color: whitesmoke;
}
li{
    list-style-type: none;
    padding: 3px;
    margin-left: -40px;
    display:flex;
    justify-content: space-between;
}
